@import url("https://fonts.googleapis.com/css2?family=Nunito&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
  scroll-behavior: smooth;
}

/* partea 1 */
.zona1 {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  width: 100%;
  height: 100vh;
  

  .poza{
    background:radial-gradient(rgba(7, 5, 5, 0.7),#222020f8),url("/images/contact.jpg");
    border-bottom-left-radius: 20px;
    background-size: cover;
    background-position: center;
    height: 100%;
    width: 50%;
    display: flex;
    align-items: center;
    
    
  }

  .text-zona1{
    display: flex;
    align-items: center;
    justify-content: center;
    
    width: 50%;
    height: 100%;

  
  }
}
.zona1 .img {
  width: 100%;
  max-width: 760px;
  height: auto;
  border-radius: 3px;
  height: auto;
  max-height: 760px;
  box-shadow: -10px 10px 20px  rgba(0, 0, 0, 0.5);
  transform: translatex(-20%);
  
}

.zona1 .text {
  
  
  width: 80%;

  a{
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    background:#444/* radial-gradient(#c45e5e,#d39a9a) */;
    height: 75px;
    border-radius: 7px;
    width: 100%;
    max-width: 200px;
    /* border-radius: 10px; */
    border: 1px solid rgba(0,0,0,20%);
    
    margin-top: 5%;
    color: whitesmoke;
    transition: 0.7s all ease;
    margin-top: 15%;
  }

  a:hover{
    
    background: whitesmoke;
    color:#c45e5e;
    font-weight: bolder

  }
}
.zona1 .text p {
  display: block;
  width: 100%;
}

.text{
  text-align: left;
  
  background-color: #222;
  border-radius: 20px;
  height: 80%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
  h1{
    text-align: center;
    color: whitesmoke;
    text-shadow: 2px 2px 4px black;
    font-size: 2rem;
    padding-top: 5rem;
  }

  p{
    color: whitesmoke;
    text-shadow: 2px 2px 4px black;
    font-size: 1.2rem;
    padding: 4rem;
  }
}

