.text-home-page{
  display: flex;
  justify-content: center;
}

.text-center{
  width: 80%;
  margin-top: 1%;
}

.content-servicii{
  display: flex;
  justify-content: center;
}

.servicii-home{
  margin: 4rem 6rem;
  max-width: 1920px;

  .photo{
    position: relative;
    width: 50%;
    display: flex;
    justify-content: space-between;
    z-index: -99;

    img{
      padding: 0.5rem;
      width: 49%;
      height: 350px;
      object-fit:contain;
      border-radius: 20px;
      box-shadow: -1px -1px 62px -18px rgba(0, 0, 0, 0.19);
      &:nth-child(2){
        transform: translateY(-10%);
      }
    }
  }


  h1{
    font-size: 3rem;
  }

  .first-serv{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;

    .des-text{
      width: 45%;
      text-align: start;
      font-size: 1.1rem;

      h2{
        padding: 2rem 0;
      }
    }
  }
}


@media screen  and (max-width:964px){
  .servicii-home{
    margin: 4rem 2rem ;

    .first-serv{
      display: flex;
      flex-direction: column-reverse;
      align-items: center;
      justify-content: space-between;
      margin-top: 2.5rem;

      .des-text{
        width: 100%;
      }

      .photo{
        width: 100%;
        margin: 1.8rem 0;

        img{
          height: 250px;
        }
      }
    }
  }
}


/* terapeuti */

.terapeuti-bck {
  /* background-image: url('../../images/elegant2.jpg'); */
  background-image: linear-gradient(rgba(4,9,30,0.7),rgba(4,9,30,0.7)),url('../../images/beautiful-nature-7862496.jpg');
  height: 100%;
  width: 100%;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
}

.terapeuti {
  
  width: 80%;
  max-width: 1920px;
  margin: auto;
  text-align: center;
  padding-top: 100px;
}

  .terapeuti h1 {
    font-family: 'Wix Madefor Display', sans-serif;
    color: whitesmoke;
    padding-bottom: 4rem;
    text-shadow: 6px 6px 10px rgb(117, 117, 117);
  }


.rand-ter {
  margin-top: 5%;
  display: flex;
  justify-content: space-between;
  
}



.terapeuti-col {
  
  flex-basis: 32%;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  padding-bottom: 3rem;
}

  .terapeuti-col img {
    box-shadow: 20px 20px 20px rgba(20, 20, 20, 0.699);
    border: 7px solid #222;
    border-radius: 20px;
      width: 80%;
    background-color: #333;
    
    
  }


.layer {
  background: transparent;
  height: 90%;
  width: 80%;
  position: absolute;
  top: 0;
  
  transition: 0.7s;
}

  .layer h3 {
      width: 100%;
      font-weight: 500;
      color: #fff;
      font-size: 26px;
      bottom: 10px;
      left: 50%;
      transform: translateX(-50%);
      position: absolute;
      opacity: 0;
      transition: 0.7s;
      background-color: #a0bf54;
      width: 80%;
      border-radius: 7px;
  }

  .layer:hover h3 {
      bottom: 49%;
      opacity: 1;
  }

  .layer p {
      width: 100%;
      font-weight: 900;
      color: #1f1e1e;
      font-size: 1rem;
      bottom: 10px;
      left: 50%;
      top: 65%;
      transform: translateX(-50%);
      position: absolute;
      opacity: 0;
      transition: 0.7s;
      margin-top: 10%;
      
  }

  .layer:hover p {
      bottom: 49%;
      opacity: 1;
  }

@media(max-width: 767px) {
  .rand-ter {
      flex-direction: column;
  }


  .terapeuti-bck {
      height: 250%;
  }

  .layer {
      height: 100%;
  }

  .terapeuti h1 {
      margin-top: -70px;
  }
}

